body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-style ul {
  display: flex;
  list-style: none;
  justify-content: center;
}

.nav-style li {
  margin-right: 20px;
  color: white;
}

.nav-style a {
  color: white;
  text-decoration: underline !important;
}

*{
  margin: 0;
  padding: 0;
  border:0;
  box-sizing: border-box;
}

body{
  font-size:14px;
}

a{
  color:black;
  text-decoration: none;
  cursor: pointer;
}

li{
  list-style-type: none;
}

input{
  outline:none;
}


.auth{
  width: 100vw;
  height: 93vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.auth form{
  padding-top:10vh; 
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  
}

.auth h3{
  font-size: 1.8em;
  margin-bottom: 50px;
}

.auth form input{
  font-size: 1.6em;
  border :2px solid lightgrey;
  border-radius: 7px;
  padding: 10px 15px;
  margin-bottom: 20px;
  transition: 0.3s all ease;
}

.auth form input:hover, .auth form input:focus{
  color: white;
  background: black;

}

input[type="submit"]{
  cursor: pointer;
}

.container{
  width: 100%;
  height: 93vh;
  display: flex;
}


nav{
	width:100%;
	height: 7vh;
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	align-items: center;
	box-shadow: 0 0 3px 0 grey;
}


nav h1{
	margin-left:4% ;
	font-size:2em;
}
nav ul{
	width: 100%;
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	align-items: center;
	padding-right:50px;
	font-size: 1.3em;
	
}

nav ul li{
margin-right: 20px;
transition: 0.3s all ease;
}

nav ul li a{
	transition: 0.3s all ease;
}

nav ul li:hover a{
	color:grey;
}
.selector{
  width: 15%;
  height: 100%;
  padding-top:5%;
  text-align: center; 
  background: #312d2d;
  color:white;
  font-size: 1.8em;
}

.selector a{
  color: white;
}

.selector ul{
  margin-top:80px;
}

.selector ul li{
    text-align: start;
    /* padding-left:10%; */
    margin-left:10%;
  margin-bottom:15px;
  cursor: pointer;
  transition: 0.3s all ease;
}

.selector ul>:first-child{
    padding: 0;
    margin: 0;
 text-align: center;
  margin-bottom:30px;
  cursor: default;
}
.pet:hover{
    -webkit-transform: scale(1.3)
    translateX(30px);
            transform: scale(1.3)
    translateX(30px)
}
.thread{
    width:85%;
    
}
.thread h4{
    margin-top:50px;
    font-size:3em;
   text-align: center;

}
.thread h5{
    font-size:2em;
   text-align: center;
    margin-top:10px;
}
.thread .charts{
    display:flex;
     margin-top:50px;
    justify-content: space-around;
}

.thread .charts>div{
    width:30%;
    height: 200px;
    
}

.thread .charts img{
    width:80%;
    height: 200px;
    
}
canvas{
    width: 30% !important;
    height: 200px !important;
    background: white;
    transition: 0.3s all ease;
   

}
.options{
    width:100%;
    display: flex;
    justify-content: space-around;
    margin-top:100px;

}
.options a{
    width:20%;
}
.options div{
    width:100%;
    min-height: 200px;
    /* border:1px solid black; */
    padding:30px;
    font-size:2em;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0 0px 4px 0px grey;
    transition: 0.3s all ease;

}

.options div:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    box-shadow: 0 0px 14px 0px grey;
}

.generals{
    font-size: 1.3em;
    display: flex;
    flex-direction:column;
    justify-content: space-around;
    padding: 0 0 0 100px;
}

.generals>:first-child{
    font-weight: 600;
}

.nopet{
    width: 50%;
    margin: 10%  auto;
    color:grey;
    font-size: 4em;
    padding: 5%;
    border-radius:10px;
    border :3px dashed grey;
    text-align:center;
    opacity: 0.7;
}
 i.fa{
     margin-left:15px;
     cursor:pointer;
 }
 form.generals {
     width:30%;
     
    display: flex;
    flex-direction:column;
    justify-content: space-around;
   
    

 }

 .generals input{
     width:50%;
     font-size: 1.1em;
       border:1px solid lightgrey;
       margin-top:20px;
 }


h4{
    text-align: center;
    font-size: 2.5em;
    margin:20px 0;
}

.consults{
width: 85%;
position: relative;
overflow-y:scroll;
height: 93vh;
}

.consults .container{
    display: flex;
    flex-direction: row;
    padding: 0 30px;
    height: auto;

}
i.fa.fa-arrow-left{
    position: absolute;
    left:3%;
    top:3%;
    color:lightgrey;
    font-size:2em;
    transition:0.3s all ease;
    cursor: pointer;
}

i.fa.fa-arrow-left:hover{
    color:black;
    -webkit-transform: scale(1.3);
            transform: scale(1.3)
}
.consults div.hilo{
     width:20%;
    height: 200px;
    /* border:1px solid black; */
    padding:30px;
    margin: 30px;
    font-size:2em;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0 0px 4px 0px grey;
    transition: 0.3s all ease;
    cursor: pointer;
}

.consults div.hilo:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    box-shadow: 0 0px 14px 0px grey;
}

.containerdos{
    margin-top:40px;
}
.consults .containerdos .consulta{
    width:80%;
    margin:0 auto 80px;
    border-radius: 10px;
    padding: 0 5% 20px;
    box-shadow: 0 0px 4px 0px grey;
}
.consulta .fecha{
    display:flex;
    border: 1px  solid #312d2d;
    color: white;
    border-top:0;
    background: #312d2d;
    border-radius: 0 0 5px 5px;
    justify-content: space-between;
    padding: 10px 30px;
    font-size: 1.4em;

}


.description{
    margin-top:40px;
    font-size: 1.1em;
    line-height: 1.3em
}

.indications{
    margin-top:60px;
    margin-bottom: 20px;
    font-size: 1.1em;
    line-height: 1.3em
}
.consulta a{
    margin: 0 50%;
    font-size :1.5em;
    color:lightgrey;
    transition: 0.3s all ease;
}

.consulta a:hover{
    margin: 0 50%;
    font-size :1.5em;
    color:black;
}
.fa{
margin-top:40px;
}



div.form{
    width:100%;
    height:100%;
    -webkit-transform:scale(1);
            transform:scale(1);
    position:absolute;
    top:-100%;
    left:0;
    transition:0.7s all ease;
    /* background: red; */
    z-index: 5;
}
div.form.show{
    top:0;
    -webkit-transform: scale(1);
            transform: scale(1);
}

div.form .filtro{
    width: 100%;
    height: 100%;
    background: black;
    opacity: 0.7;
}
.form form{
    width:30%;
    position: absolute;
    top:20%;
    left: 35%;
    z-index: 6;
    padding: 50px;
    background:white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size:1.5em;
}
.form .fa{
    color:white;
    font-size: 3em;
    position: fixed;
    top:3%;
    right:10%;
}
.form form input{
    width:80%;
    padding:10px 15px;
    font-size:1em;
    margin: 20px 0 0;
    border:1px solid lightgrey;
}

.form input[type="submit"]{
    width:50%;
    padding:5px 7px;
    font-size:0.8em;
    transition: 0.3s all ease;
}

.form input[type="submit"]:hover{
    background:rgba(51,109,233,1);
    color:white;
    -webkit-transform:scale(1.1);
            transform:scale(1.1);
}


h5{
    text-align: center;
    font-size: 1.2em;
    cursor:pointer;
}


@media print {
   .consulta .fecha{
    display:flex;
    border: 1px  solid #312d2d;
    color: white;
    border-top:0;
    background: #312d2d;
   border-radius: 0;
    justify-content: space-between;
    padding: 10px 30px;
    font-size: 1.4em;

}

.description{
    padding:0 50px;
}

.indications{
   padding:0 50px;
}
.fa {
    display: inline-block;
    color: transparent;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

}

.reservations{
    width: 85%;
    height: 93vh;
    display: flex;
    justify-content: center;
    position: relative;
}
.reservations h4{
    font-weight: 400;
}
.reservations form{
    
    width:50%;
    display: flex;
    flex-direction: column;
    height: 100%;
    padding-top:10%;
    align-items: center;
    font-size: 1.2em;
    

}

.reservations form input{
    width:30%;
    padding: 10px 20px;
    border-radius:3px;
    text-align: center;
    font-size: 1.01em;
    border:1px solid lightgrey;
}

.reservations form label{
    margin-top:40px;
    margin-bottom: 10px;
}

.reservations input[type="submit"]{
    width: 200px;

    
    margin:60px 30px 0;
    transition:0.3s all ease;
    
}

.reservations input[type="submit"]:hover{
    background:rgba(51,109,233,1);
    border:0;
    color:white;
    -webkit-transform: scale(1.2);
            transform: scale(1.2)
}
.final{
    margin-top: 50px;
    font-size: 1.6em;
    color: green;
}

.submit{
    display: flex;
    justify-content: center;
}




.vet{
  width: 15%;
  height: 100%;
  padding:5% 20px 0;
  text-align: center; 
  background: #312d2d;
  color:white;
  font-size: 1.8em;
  display: flex;
  flex-direction: column;
  overflow-y:scroll;
}

.vet input{
    padding: 10px 5px;
    margin-top:20px;
    width: 80%;
    border-radius:5px;
}

.vet ul{
    margin-top:50px;
}

.vet ul li>:last-child{
    font-size:0.5em;
}

.vet ul>:first-child{
    margin-bottom: 50px;
    cursor: default;
}

.vet ul li{
    margin-top: 30px;
    cursor:pointer;
    transition: 0.3s all ease;
}
.pet{
     text-align: start;
}
.pet:hover{
    -webkit-transform: scale(1.1)
    translateX(30px);
            transform: scale(1.1)
    translateX(30px)
}


